.ellipse2-container{
    position: relative;
    border-radius: 50%;
    border: 2px solid #9833D9;
    box-shadow: 0px 0px 50px rgba(152, 51, 217, 0.5);
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

.ellipse2-imagem{
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
}